<template>
  <main>
    <b-container class="py-3" fluid>
      <b-row>
        <b-col cols="12" md="10" offset-md="1">
          <ContentDoc :value="data">
            <ContentRendererMarkdown :value="data.body" />
            <template #not-found>
              Not Found
            </template>
          </ContentDoc>
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script setup>
definePageMeta({
  layout: "blog"
});

const route = useRoute();

const { data, error } = await useAsyncData(`pages-${route.path}`, () => {
  return queryContent().where({ _path: route.path }).findOne();
});

if (error.value) console.log("Error: %o", Object.keys(error.value))

useContentHead(data)
</script>
